import './Login.css'
import './assets/bootstrap/css/bootstrap.min.css'
import './assets/fonts/ionicons.min.css'
import './assets/css/Login-Form-Clean.css'
import './assets/css/styles.css'
import React, {useRef, useState}from 'react'
import './assets/css/Registration-Form-with-Photo.css'
import { useAuth } from './AuthContext';
import {createUserWithEmailAndPassword, onAuthStateChanged, signInWithEmailAndPassword} from 'firebase/auth'
import { auth} from '../Firebase'
import {Link} from 'react-router-dom'
function Login() {
    const[loginEmail, setLoginEmail] = useState("")
    const[loginPassword, setLoginPassword] = useState("")
    const login = async() => {
        try{
            const user = await signInWithEmailAndPassword(auth, loginEmail, loginPassword);
        }catch(error){
            alert(error.message)
            console.log(error.message)
        }
    };
    return (
        <section class="login-clean">
        <div class="form">
            <h2 class="visually-hidden">Login Form</h2>
            <div class="illustration"><i class="icon ion-ios-navigate"></i></div>
            <div class="mb-3"><input class="form-control" type="email" name="email" placeholder="Email" onChange={(event) => {setLoginEmail(event.target.value)}}/></div>
            <div class="mb-3"><input class="form-control" type="password" name="password" placeholder="Password" onChange={(event) => {setLoginPassword(event.target.value)}}/></div>
            <div class="mb-3"><button class="btn btn-primary d-block w-100" type="submit" onClick={login}><Link to="/" style={{color:'white'}}>Log In</Link></button></div><a class="forgot" href="#">Forgot your email or password?</a>
        </div>
    </section>
    )
}

export default Login
