import React from 'react'
import { useState } from 'react'
function Countdown() {
    const [day, setday] = useState('00')
    const [hour, sethour] = useState('00')
    const [minute, setminute] = useState('00')
    const [second, setsecond] = useState('00')
    setInterval(() =>{
        var now = new Date().getTime();
    var countdowndate = new Date("January 1, 2022 00:00:00").getTime();
    var seconds = countdowndate-now
    var d = Math.floor(seconds / (1000*60*60*24));
    seconds = seconds%(1000*60*60*24)
    var h = Math.floor(seconds / (1000*60*60));
    seconds = seconds%(1000*60*60)
    var m = Math.floor(seconds % (1000*60*60) / (1000*60));
    seconds = seconds%(1000*60)
    var s = Math.floor(seconds / 1000);
        setday(d);
        sethour(h);
        setminute(m);
        setsecond(s);
    },1000)
    return (
        <div>
            <section class="py-5 bg-info text-white">
        <div class="container text-center boxed-countdown" data-countdown="11/27/2020 02:51:00">
            <div class="row">
                <div class="col-12 mb-3">
                    <h1>New Year count down</h1>
                    <p><br></br></p>
                </div>
                <div class="col-6 col-md-3 mt-3">
                    <p class="number-days m-0 rounded-top">{day}</p>
                    <p class="text-countdown rounded-bottom">days</p>
                </div>
                <div class="col-6 col-md-3 mt-3">
                    <p class="number-hours m-0 rounded-top">{hour}</p>
                    <p class="text-countdown rounded-bottom">hours</p>
                </div>
                <div class="col-6 col-md-3 mt-3">
                    <p class="number-minutes m-0 rounded-top">{minute}</p>
                    <p class="text-countdown rounded-bottom">minutes</p>
                </div>
                <div class="col-6 col-md-3 mt-3">
                    <p class="number-seconds m-0 rounded-top">{second}</p>
                    <p class="text-countdown rounded-bottom">seconds</p>
                </div>
            </div>
        </div>
        <div class="container">
            <p class="text-center mt-5" data-countdown="08/27/2020 04:12:32"><br/></p>
        </div>
    </section>
        </div>
    )
}

export default Countdown
